
.ratio_banner {
  position: relative;
  picture {
    display: block;
    z-index: 1;
    position: relative;
  }
  .margery {
    margin-top: 40px;
  }
  &.letterbox {
    picture {
      top: 0;
    }
    aspect-ratio: 1 / 1;
    @media screen and (min-width: 880px) {
      aspect-ratio: 12 / 5;
      padding-top: (5 / 12) * 100%;
      max-width: 100%;
      picture {
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        inset: 0 0 0 0;
        position: absolute;
        //z-index: 2;
      }
    }
  }
  .heroshot img {
    width: 100%;
    display: block;
    aspect-ratio: 1 / 1;
    object-fit: cover;
    @media screen and (min-width: 880px) {
      aspect-ratio: 512 / 301;
    }
    @media screen and (min-width: 1200px) {
      aspect-ratio: 84 / 41;
    }
    @media screen and (min-width: 1680px) {
      aspect-ratio: 64 / 25;
    }
  }
  .letterbox img {
    width: 100%;
    display: block;
    aspect-ratio: 1 / 1;
    object-fit: cover;
    @media screen and (min-width: 880px) {
      aspect-ratio: 12 / 5;
    }
  }
  .cta {
    position: absolute;
    left: 0;
    right: 0;
    top: 90px;
    bottom: 0;
    z-index: 2;
    .text_box {
      max-width: 80vw;
    }
    @media screen and (min-width: 880px) {
      .text_box {
        max-width: 800px;
        flex: 1 0 800px;
        &.width-600 {
          max-width: 600px;
          flex-basis: 600px;
        }
        &.width-700 {
          max-width: 700px;
          flex-basis: 700px;
        }
        &.width-900 {
          max-width: 900px;
          flex-basis: 900px;
        }
        &.width-1000 {
          max-width: 1000px;
          flex-basis: 1000px;
        }
      }
    }
  }
  .text, .buttons {
    margin: 16px;
  }
  .text {
    &.large {
      font-size: 200%;
    }
    &+.buttons {
      margin-top: 32px;
    }
  }
}
@media screen and (min-width: 880px) {
  .collection_header .ratio_banner picture.letterbox {
    top: 0;
  }
}
nav.link {
  padding: 8px 0;
  @media screen and (min-width: 768px) {
    padding: 16px 0;
  }
}
.text_box {
  nav.link {
    &:first-child {
      padding-top: 0;
    }
  }
}