:root {
  --font-body-family: "Spartan", sans-serif;
  --font-body-style: normal;
  --font-body-weight: 400;
  --font-heading-family: "Sorts Mill Goudy", serif;
  --font-heading-style: normal;
  --font-heading-weight: normal;
  --color-base-text: #CA1E2A;
  --color-base-background-1: white;
  --color-base-background-2: #F3F2F0;
  --color-base-solid-button-labels: #CA1E2A;
  --color-base-outline-button-labels: #CA1E2A;
  --color-base-accent-1: #CA1E2A;
  --color-base-accent-2: #CA1E2A;
  --color-background: 255,255,255;
  --color-foreground: #CA1E2A;
  --color-link: #ca1e2a;
  --color-menu: #1A1A1A;
  --color-menu-hover: #000000;
  --color-dark: #000000;
  --color-red: #CA1E2A;
  --color-red-rgb: 202,30,42;
  --color-blue: #034975;
  --color-blue-rgb: 3,73,117;
  --color-yellow: #E1AF53;
  --color-yellow-rgb: 225,175,83;
  --color-pink: #FD717B;
  --color-pink-rgb: 253,113,123;
  --color-lightpink: #FFC7CA;
  --color-lightpink-rgb: 255,199,202;
  --color-green: #8DC687;
  --color-green-rgb: 141,198,135;
  --color-shade-dark: #BEB3A2;
  --color-shade: #DDD7CF;
  --color-shade-light: #EBE7E3;
  --color-burger: #fff;
  --transition-fast: 0.2s all cubic-bezier(0.32, 0.24, 0.15, 1);
  --transition: 0.35s all cubic-bezier(0.32, 0.24, 0.15, 1);
  --transition-slow: 0.6s all cubic-bezier(0.32, 0.24, 0.15, 1);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

/* component-grid */
.grid {
  display: grid;
  grid-template-rows: 1fr;
  gap: 16px;
}
@media screen and (min-width: 600px) {
  .grid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
@media screen and (min-width: 768px) {
  .grid {
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 24px;
  }
}

.flex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  align-content: stretch;
  gap: 16px;
}
@media screen and (min-width: 1024px) {
  .flex {
    gap: 24px;
  }
}
.flex.end, .flex.vertical-end {
  align-content: flex-end;
}
.flex.start, .flex.vertical-start {
  align-content: flex-start;
}
.flex.middle, .flex.vertical-center {
  align-content: center;
}
.flex.between, .flex.vertical-between {
  align-content: space-between;
}
.flex.evenly, .flex.vertical-evenly {
  align-content: space-evenly;
}
.flex.around, .flex.vertical-around {
  align-content: space-around;
}

.justify-start {
  justify-content: flex-start;
}

.justify-end {
  justify-content: flex-end;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.align-center {
  text-align: center;
}

.color-white_text.color-white_text {
  color: white;
}

.color-red_text.color-red_text {
  color: var(--color-red);
}

.color-dark_text.color-dark_text {
  color: var(--color-dark);
}

.color-blue_text.color-blue_text {
  color: var(--color-blue);
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  height: 100%;
  font-size: 12px;
}
@media screen and (min-width: 600px) {
  html {
    font-size: 14px;
  }
}
@media screen and (min-width: 1024px) {
  html {
    font-size: 16px;
  }
}

body {
  display: grid;
  grid-template-rows: auto auto 1fr auto;
  grid-template-columns: 100%;
  min-height: 100%;
  margin: 0;
  font-size: 100%;
  line-height: 1.5;
  font-family: var(--font-body-family);
  font-style: var(--font-body-style);
  font-weight: var(--font-body-weight);
  color: var(--color-base-text);
}

.inner {
  max-width: 1272px;
  margin: 0 auto;
  padding: 0 12px;
}
@media screen and (min-width: 600px) {
  .inner {
    padding: 0 16px;
  }
}
.inner.narrow {
  max-width: 702px;
}
.inner.w800 {
  max-width: 800px;
}
.inner.w900 {
  max-width: 900px;
}
.inner.w1000 {
  max-width: 1000px;
}
.inner.w1100 {
  max-width: 1100px;
}

.outer {
  padding-left: 8px;
  padding-right: 8px;
}
@media screen and (min-width: 768px) {
  .outer {
    padding-left: 16px;
    padding-right: 16px;
  }
}
@media screen and (min-width: 1024px) {
  .outer {
    padding-left: 24px;
    padding-right: 24px;
  }
}
@media screen and (min-width: 1860px) {
  .outer {
    max-width: 1920px;
    margin: 0 auto;
  }
}

/* base */
.no-js:not(html) {
  display: none !important;
}

html.no-js .no-js:not(html) {
  display: block !important;
}

.no-js-inline {
  display: none !important;
}

html.no-js .no-js-inline {
  display: inline-block !important;
}

html.no-js .no-js-hidden {
  display: none !important;
}

.page-width-desktop {
  padding: 0;
  margin: 0 auto;
}

.page-margin,
.shopify-challenge__container {
  margin: 7rem auto;
}

.rte-width {
