}

.nude.hamburger {
  position: fixed;
  z-index: 9999;
  left: -7px;
  top: 1px;
  padding: 15px 15px;
  display: inline-block;
  cursor: pointer;
  min-width: 40px;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
}
