.js {
  .lazyimage {
    opacity: 1;
  }
  .lazyimage.lazyImageWaiting {
    opacity: 0;
    transition: 0.4s all cubic-bezier(0.32, 0.24, 0.15, 1);
  }
  .spaced-section {
    opacity: 0;
    transition: 0.4s all cubic-bezier(0.32, 0.24, 0.15, 1) 0.9s;
  }
  .header_links {
    opacity: 0;
    transform: translateY(-8px);
    transition: 0.4s all cubic-bezier(0.32, 0.24, 0.15, 1) 0s;
  }
  .header__inline-menu {
    opacity: 0;
    transform: translateY(-8px);
    transition: 0.4s all cubic-bezier(0.32, 0.24, 0.15, 1) 0.3s;
  }
  .logo_banner {
    opacity: 0;
    transform: translateY(8px);
    transition: 0.4s all cubic-bezier(0.32, 0.24, 0.15, 1) 0.5s;
  }
  .text_banner_section, .collection_header {
    opacity: 0;
    transition: 0.4s all cubic-bezier(0.32, 0.24, 0.15, 1) 0.6s;
  }
  .basic-page {
    opacity: 0;
    transform: translateY(8px);
    transition: 0.4s all cubic-bezier(0.32, 0.24, 0.15, 1) 0.7s;
  }
  .image_banner {
    opacity: 0;
    transition: 0.4s all cubic-bezier(0.32, 0.24, 0.15, 1) 0.3s;
  }
  // Ding the site is ready
  .ding {
    .header__inline-menu, .logo_banner, .header_links, .image_banner, .basic-page, .spaced-section, .collection_header, .text_banner_section {
      opacity: 1;
      transform: translateY(0);
    }
  }
}

@keyframes jello-horizontal {
  0% {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
            opacity: 0;
  }
  30% {
    -webkit-transform: scale3d(1.25, 0.75, 1);
            transform: scale3d(1.25, 0.75, 1);
            opacity: 1;
  }
  40% {
    -webkit-transform: scale3d(0.75, 1.25, 1);
            transform: scale3d(0.75, 1.25, 1);
  }
  50% {
    -webkit-transform: scale3d(1.15, 0.85, 1);
            transform: scale3d(1.15, 0.85, 1);
  }
  65% {
    -webkit-transform: scale3d(0.95, 1.05, 1);
            transform: scale3d(0.95, 1.05, 1);
  }
  75% {
    -webkit-transform: scale3d(1.05, 0.95, 1);
            transform: scale3d(1.05, 0.95, 1);
  }
  100% {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }
}

@keyframes jump-in-1 {
  0% {
  opacity: 0;
  }
  10% {
    opacity: 0;
  }
  11% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
