.nav-section {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(255,255,255,0.97);
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.05);
  width: 100%;
  max-width: 852px;
  z-index: 9999;
  overflow-y: auto;
  color: var(--color-dark);
  transition: var(--transition);
  transform: translateX(-852px);
  &.active {
    transform: translateX(0);
  }
  nav {
    > .flex {
      border-top: 1px solid rgba(196, 196, 196, 0.25);
      padding: 24px 0 8px 0;
    }
  }
  .nav_container {
    padding: 64px 16px;
    @media screen and (min-width: 768px) {
      padding: 64px 24px 64px;
    }
  }
  a {
    color: var(--color-dark);
  }
  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    li {
      line-height: 1.5;
      padding: 0 0 12px 0;
    }
    a.hasChildren {
      display: block;
      line-height: 1.5;
      padding: 0 0 12px 0;
    }
    &.sub_links li {
      font-family: var(--font-heading-family);
    }
  }
  img {
    display: block;
  }
  p {
    margin: 8px 0;
    font-family: var(--font-heading-family);
  }
  .colour_links {
    margin: 16px 0 8px 0;
    justify-content: flex-start;
    a {
      height: 24px;
      width: 24px;
      flex: 0 1 24px;
      img {
        border-radius: 12px;
      }
    }
  }
  .hero_menus {
    li {
      flex: 1;
    }
    strong {
      display: block;
      margin-bottom: 8px;
    }
    margin-bottom: 8px;
  }
  .nav_columns {
    // border-top: 1px solid rgba(196, 196, 196, 0.25);
    // padding: 24px 0 8px 0;
    .title {
      flex: 1 0 100%;
      font-weight: 700;
    }
    > div {
      flex: 1 1 50%;
      flex-basis: calc(50% - 24px);
      @media screen and (min-width: 768px) {
        flex: 1 1 25%;
        flex-basis: calc(25% - 24px);
      }
      ul.nav_col {
        > li > a {
          letter-spacing: -0.03em;
          font-weight: 600;
          font-size: 87.5%;
        }
        &+.nav_col {
          margin-top: 26px;
        }
      }
    }
  }
  nav > .four_menus.flex {
    position: relative;
    padding-bottom: 18px;
    &.with_t {
      padding-top: 40px;
      padding-bottom: 12px;
    }
    .navtitle {
      position: absolute;
      left: 0;
      top: 8px;
      font-size: 93.5%;
      font-family: var(--font-heading-family);
      color: var(--color-green);
    }
    > a {
      flex: 1 1 50%;
      flex-basis: calc(50% - 24px);
      letter-spacing: -0.03em;
      font-weight: 600;
      font-size: 87.5%;
      @media screen and (min-width: 768px) {
        flex-basis: calc(25% - 24px);
      }
    }
  }
}

.navcloser {
  position: fixed;
  left: 0;
  top: 0;
  z-index: -1;
  .nav-active & {
    inset: 0;
    z-index: 11;
  }
}

// Burgers nom nom
@charset "UTF-8";
/*!
 * Hamburgers
 * @description Tasty CSS-animated hamburgers
 * @author Jonathan Suh @jonsuh
 * @site https://jonsuh.com/hamburgers
 * @link https://github.com/jonsuh/hamburgers
 */

// Settings
// ==================================================
$hamburger-padding-x           : 15px !default;
$hamburger-padding-y           : 15px !default;
$hamburger-layer-width         : 32px !default;
$hamburger-layer-height        : 2px !default;
$hamburger-layer-spacing       : 8px !default;
$hamburger-layer-color         : #fff !default;
$hamburger-layer-border-radius : 0px !default;
$hamburger-hover-opacity       : 0.7 !default;
$hamburger-active-layer-color  : var(--color-burger) !default;
$hamburger-active-hover-opacity: $hamburger-hover-opacity !default;

// To use CSS filters as the hover effect instead of opacity,
// set $hamburger-hover-use-filter as true and
// change the value of $hamburger-hover-filter accordingly.
$hamburger-hover-use-filter   : false !default;
$hamburger-hover-filter       : opacity(50%) !default;
$hamburger-active-hover-filter: $hamburger-hover-filter !default;


.template-page, .template-product, .template-collection, .template-cart {
  --color-burger: white;
}
// Types (Remove or comment out what you dont need)
// ==================================================
$hamburger-types: (
  spring
) !default;

// Hamburger
// ==================================================
.nude.hamburger {
  // @media screen and (min-width:800px) {
  //   display: none;
  //   visibility: hidden;
  // }
  position: fixed;
  z-index: 9999;
  left: -7px;
  top: 1px;
  padding: $hamburger-padding-y $hamburger-padding-x;
  display: inline-block;
  cursor: pointer;
  min-width: 40px;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;

  // Normalize (<button>)
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
  @media screen and (min-width: 420px) {
    left: 1px;
  }
  @media screen and (min-width: 800px) {
    left: 9px;
  }
  &:hover {
    @if $hamburger-hover-use-filter == true {
      filter: $hamburger-hover-filter;
    }
    @else {
      opacity: $hamburger-hover-opacity;
    }
  }

  &.is-active {
    &:hover {
      @if $hamburger-hover-use-filter == true {
        filter: $hamburger-active-hover-filter;
      }
      @else {
        opacity: $hamburger-active-hover-opacity;
      }
    }

    .hamburger-inner,
    .hamburger-inner::before,
    .hamburger-inner::after {
      background-color: var(--color-dark);
    }
  }
}

.hamburger-box {
  width: $hamburger-layer-width;
  height: $hamburger-layer-height * 3 + $hamburger-layer-spacing * 2;
  display: inline-block;
  position: relative;
}

.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: $hamburger-layer-height / -2;

  &,
  &::before,
  &::after {
    width: $hamburger-layer-width;
    height: $hamburger-layer-height;
    background-color: var(--color-burger);
    border-radius: $hamburger-layer-border-radius;
    position: absolute;
    transition-property: transform;
    transition-duration: 0.15s;
    transition-timing-function: ease;
  }

  &::before,
  &::after {
    content: "";
    display: block;
  }

  &::before {
    top: ($hamburger-layer-spacing + $hamburger-layer-height) * -1;
  }

  &::after {
    bottom: ($hamburger-layer-spacing + $hamburger-layer-height) * -1;
  }
}

// Hamburger types
// ==================================================

@if index($hamburger-types, spring) {
  /*
   * Spring
   */
  .hamburger--spring {
    .hamburger-inner {
      top: $hamburger-layer-height / 2;
      transition: background-color 0s 0.13s linear;

      &::before {
        top: $hamburger-layer-height + $hamburger-layer-spacing;
        transition: top 0.1s 0.2s cubic-bezier(0.33333, 0.66667, 0.66667, 1),
                    transform 0.13s cubic-bezier(0.55, 0.055, 0.675, 0.19);
      }

      &::after {
        top: ($hamburger-layer-height * 2) + ($hamburger-layer-spacing * 2);
        transition: top 0.2s 0.2s cubic-bezier(0.33333, 0.66667, 0.66667, 1),
                    transform 0.13s cubic-bezier(0.55, 0.055, 0.675, 0.19);
      }
    }

    &.is-active {
      .hamburger-inner {
        transition-delay: 0.22s;
        background-color: transparent !important;

        &::before {
          top: 0;
          transition: top 0.1s 0.15s cubic-bezier(0.33333, 0, 0.66667, 0.33333),
                      transform 0.13s 0.22s cubic-bezier(0.215, 0.61, 0.355, 1);
          transform: translate3d(0, $hamburger-layer-spacing + $hamburger-layer-height, 0) rotate(45deg);
          background-color: var(--color-dark);
        }

        &::after {
          top: 0;
          transition: top 0.2s cubic-bezier(0.33333, 0, 0.66667, 0.33333),
                      transform 0.13s 0.22s cubic-bezier(0.215, 0.61, 0.355, 1);
          transform: translate3d(0, $hamburger-layer-spacing + $hamburger-layer-height, 0) rotate(-45deg);
          background-color: var(--color-dark); 
        }
      }
    }
  }
}
